header {
  padding: 5%;
}

header > div > a > i {
  padding: 10px;
  color: #2f3441;
}

header > div > a > i:hover {
  color: #eee;
  cursor: pointer;
}

.resume {
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  font-family: 'Poiret One', 'cursive';
  font-size: 20px;
  margin: 0 auto;
}



